.Guide {
  display: none; }
  .Guide .col {
    border: 1px solid black;
    display: inline-block;
    min-width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center; }
  .Guide .row {
    vertical-align: top;
    min-height: 40px;
    display: inline-grid; }
  .Guide .rows .col {
    -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
            transform: rotate(90deg); }
  .Guide .divider {
    display: block;
    min-height: 20px; }
  .Guide .qr-code {
    display: none; }

@media print {
  .no-print {
    display: none !important; }
  .Guide {
    display: block !important;
    position: absolute; }
    .Guide .col {
      min-width: 0.75in;
      height: 0.75in;
      line-height: 0.75in;
      font-size: 0.5in; }
    .Guide .row {
      min-height: 0.75in; }
    .Guide .divider {
      display: inline-block;
      min-width: 0.5in; }
    .Guide .qr-code {
      display: inline-block;
      height: 0.7in;
      width: 0.7in; } }



